/**************************************
 * Progress & activity
 *
 * http://www.google.com/design/spec/components/progress-activity.html
 **************************************/

.sc-determinate, .sc-indeterminate, .sc-buffer {
	width: 100%;
	height: rem-calc(5px);
	position: relative;
}

.sc-determinate {
	background: $progressbar-background;
}

.sc-progressbar {
	background: $progress-background;
	@include transition(width $transition-small-duration $basic-transition);
	bottom: 0;
	position: absolute;
	top: 0;
	width: 0%;
}

.sc-indeterminate {
	background: $progressbar-background;
	overflow: hidden;

	.sc-progressbar {
		width: 60%;
		@include animation(indeterminate 1.5s  cubic-bezier(0.65, 0.815, 0.735, 0.395) infinite);
		@include keyframes(indeterminate) {
			0% {
				left: 0;
				width: 0%;
			}

			25% {
				width: 60%;
			}

			50% {
				width: 60%;
			}

			75% {
				left: 75%;
				width: 60%;
			}

			100% {
				left: 100%;
				width: 0%;
			}
		}
	}
}

.sc-buffer {
	overflow: hidden;

	> div {
		display: inline-block;
	}

	.sc-buffer-progress {
		background: $progressbar-background;
		bottom: 0;
		position: absolute;
		top: 0;
	}

	.sc-buffering {
		background-image: linear-gradient(to right,rgba(255,255,255,.7),rgba(255,255,255,.7)),linear-gradient(to right,$main-color,$main-color);
		bottom: 0;
		position: absolute;
		@include mask(url('data:image/svg+xml;base64, '));
		top: 0;
		@include animation(buffer 1.5s linear infinite);
		@include keyframes(buffer) {
			0% {
				width: 200%;
				left: 100%;
			}
			100% {
				left: auto;
			}
		}
	}
}

.sc-circular {
	position: relative;
	margin: 0 auto;
	width: $circular-width;

	&:before {
		content: '';
		display: block;
		padding-top: 100%;
	}

	svg {
		@include animation(rotate 2s linear infinite);
		@include transform-origin(center center);
		height: 100%;
		width: 100%;
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		margin: auto;
	}

	circle {
		@include animation(dash 1.5s ease-in-out infinite unquote(',') color 6s ease-in-out infinite);
		stroke-dasharray: 1, 200;
		stroke-dashoffset: 0;
		stroke-width: $circular-path-width;
		r: 20;
	}

	@include keyframes(rotate) {
		100% {
			transform: rotate(360deg);
		}
	}

	@include keyframes(dash) {
		0% {
			stroke-dasharray: 1, 200;
			stroke-dashoffset: 0;
		}
		50% {
			stroke-dasharray: 89, 200;
			stroke-dashoffset: rem-calc(-35px);
		}
		100% {
			stroke-dasharray: 89, 200;
			stroke-dashoffset: rem-calc(-124px);
		}
	}

	@include keyframes(color) {
		100%,
		0% {
			stroke: $circular-color1;
		}
		40% {
			stroke: $circular-color2;
		}
		66% {
			stroke: $circular-color3;
		}
		80%,
		90% {
			stroke: $circular-color4;
		}
	}
}